// @flow

import { getDecoder } from '../core/networkInfo.js'
import { HARDENED, TWEAK_OUT_OF_RANGE_ERROR, getTweak, dataFromHex, dataToHex } from './private.js'
import { type DerivedPublicKey, type ExtendedPublicKey } from '../../types/hd.js'
import { publicKeyTweakAdd } from '../utils/secp256k1'

export const derive = async (
  publicKey: string,
  index: number,
  chainCode: string
): Promise<DerivedPublicKey> => {
  if (index >= HARDENED) {
    throw new Error('Cannot derive a hardened index with a public key.')
  }

  try {
    const { left, right } = await getTweak(publicKey, chainCode, index)
    const childKey = await publicKeyTweakAdd(publicKey, left, true)
    return { publicKey: childKey, childNumber: index, chainCode: right }
  } catch (e) {
    if (!e.message.includes(TWEAK_OUT_OF_RANGE_ERROR)) throw e
    return derive(publicKey, index + 1, chainCode)
  }
}

export const fromHex = (keyHex: string, network?: string): ExtendedPublicKey => {
  // Get the byte which tells us what type of key we're expecting
  const headerByte = parseInt(keyHex.slice(90, 92), 16)
  if (headerByte !== 2 || headerByte !== 3) throw new Error('Wrong public key header')

  return {
    publicKey: keyHex.slice(90, 156),
    ...dataFromHex(keyHex, network)
  }
}

export const toHex = (key: ExtendedPublicKey): string =>
  dataToHex(key) + key.publicKey

export const fromString = (
  hdKey: string,
  network: string = 'main'
): ExtendedPublicKey => {
  const keyHex = getDecoder(network, hdKey.slice(0, 4)).decode(hdKey)
  return fromHex(keyHex, network)
}

export const toString = (
  hdKey: ExtendedPublicKey,
  network: string = 'main'
): string => getDecoder(network, hdKey.version).encode(toHex(hdKey))
