export const derive = async (
  privateKey: string,
  index: number,
  chainCode: string,
  hardened: boolean = false,
  publicKey?: string
): Promise<DerivedPrivateKey> => {
  if (hardened && index < HARDENED) index += HARDENED
  let key = `00${privateKey}`
  if (index < HARDENED) {
    key = publicKey || await toPublic(privateKey, true)
  }

  try {
    const { left, right } = await getTweak(key, chainCode, index)
    const childKey = await privateKeyTweakAdd(privateKey, left)
    return { privateKey: childKey, childNumber: index, chainCode: right }
  } catch (e) {
    if (!e.message.includes(TWEAK_OUT_OF_RANGE_ERROR)) throw e
    if (index > MAX_INDEX) index -= HARDENED
    return derive(privateKey, index + 1, chainCode, hardened, publicKey)
  }
}

export const fromHex = (keyHex: string, network?: string): ExtendedPrivateKey => {
  // Get the byte which tells us what type of key we're expecting
  const headerByte = parseInt(keyHex.slice(90, 92), 16)
  if (headerByte !== 0) throw new Error('Wrong private key header')

  return {
    privateKey: keyHex.slice(92, 156),
    ...dataFromHex(keyHex, network)
  }
}

export const toHex = (key: ExtendedPrivateKey): string =>
  dataToHex(key) + `00${key.privateKey}`

export const fromString = (
  hdKey: string,
  network: string = 'main'
): ExtendedPrivateKey => {
  const keyHex = getDecoder(network, hdKey.slice(0, 4)).decode(hdKey)
  return fromHex(keyHex, network)
}
export const toString = (
  hdKey: ExtendedPrivateKey,
  network: string = 'main'
): string => getDecoder(network, hdKey.version).encode(toHex(hdKey))
