// @flow

import { type HDPath, type Bip32Path, type Bip44Path } from '../../types/hd.js'
import { HARDENED, MAX_INDEX } from './private.js'
import { networks } from '../core/networkInfo.js'

export const parseToken = (token: string): number => {
  // Check for hardened flag
  const hardened = token[token.length - 1] === '\''
  // If hardened, we need to add the HARDENED param to the token
  const num = hardened
    ? parseInt(token.slice(0, -1)) + HARDENED
    : parseInt(token)
  return num
}

export const stringifyToken = (token: number, harden: boolean = false): string => {
  if (token >= HARDENED) {
    harden = true
    token = token - HARDENED
  }
  return harden ? `${token}'` : `${token}`
}

export const verify = (index: string | Array<string>): boolean => {
  // If it's an array, check each index and return true only if non failed
  if (Array.isArray(index)) return !!index.map(verify)
  // If it's a full path, split it then check
  if (index.includes('/')) return verify(index.split('\''))
  // Return true if it's the master index
  if (index === 'm') return true
  if (parseToken(index) > MAX_INDEX) throw new Error(`Index out of range: ${index}`)
  return true
}

export const fromString = (path: string): Array<string> => {
  try {
    const pathArr = path.split('/')
    verify(pathArr)
    return pathArr
  } catch (e) {
    e.message = `Bad path: ${path}\n\t${e.message}`
    throw e
  }
}

export const toString = (path: Array<string>): string => {
  try {
    verify(path)
    return path.join('/')
  } catch (e) {
    e.message = `Bad path: ${JSON.stringify(path)}\n\t${e.message}`
    throw e
  }
}

export const parse = (path: string): Array<number> => {
  const pathArr = fromString(path)
  if (pathArr[0] === 'm') pathArr.shift()
  return pathArr.map(parseToken)
}

export const Bip32 = {
  toString: ({ account, chain, index }: Bip32Path): string => {
    if (chain !== 0 && chain !== 1) throw new Error('Unknown chain')
    return toString([
      'm',
      stringifyToken(account),
      stringifyToken(chain),
      stringifyToken(index)
    ])
  },
  fromString: (path: string): Bip32Path => {
    const pathArr = fromString(path)
    if (pathArr.length !== 4) throw new Error('Unknown path depth')
    if (pathArr[0] !== 'm') throw new Error('Unknown path header')
    const chain = parseToken(pathArr[2])
    if (chain !== 0 && chain !== 1) throw new Error('Unknown chain')
    return {
      account: parseToken(pathArr[1]),
      index: parseToken(pathArr[3]),
      chain
    }
  }
}

export const Bip44 = {
  toString: ({ purpose, coinType, account, change, index }: Bip44Path): string => {
    if (change !== 0 && change !== 1) throw new Error('Unknown chain')
    if (index >= HARDENED) throw new Error(`Index should be smaller then ${HARDENED}`)
    return toString([
      'm',
      stringifyToken(purpose, true),
      stringifyToken(coinType, true),
      stringifyToken(account, true),
      stringifyToken(change),
      stringifyToken(index)
    ])
  },
  fromString: (path: string): Bip44Path => {
    const pathArr = fromString(path)
    if (pathArr.length !== 6) throw new Error('Unknown path depth')
    if (pathArr[0] !== 'm') throw new Error('Unknown path header')
    const purpose = parseToken(pathArr[1])
    if (purpose < HARDENED) throw new Error('Purpose has to be hardened')
    const coinType = parseToken(pathArr[2])
    if (coinType < HARDENED) throw new Error('Coin Type has to be hardened')
    const account = parseToken(pathArr[3])
    if (account < HARDENED) throw new Error('Account has to be hardened')
    const change = parseToken(pathArr[4])
    if (change !== 0 && change !== 1) throw new Error('Unknown chain')
    const index = parseToken(pathArr[5])
    if (index >= HARDENED) throw new Error('Index should not be hardened')
    return { purpose, coinType, account, change, index }
  }
}

export const createPath = (
  account: number = 0,
  parent: HDPath = { path: ['m'] },
  hardened?: boolean
): HDPath => {
  const { chain = 'external', scriptType = 'P2PKH' } = parent

  const accountStr = `${account}${hardened ? "'" : ''}`
  const index = chain === 'external' ? '0' : '1'
  const path = [...parent.path, accountStr, index]

  return { path, chain, scriptType }
}

export const createPaths = (
  purpose?: number | Array<number>,
  coinType: number = 0,
  account: number = 0,
  network: string = 'main'
): Array<HDPath> => {
  if (Array.isArray(purpose)) {
    const paths = []
    for (const p of purpose) {
      paths.push(...createPaths(p, coinType, account, network))
    }
    return paths
  }

  const { HDPaths, DefaultHDPath } = networks[network]
  if (!purpose) purpose = DefaultHDPath

  if (purpose === 32) return [createPath(account)]

  const pathSettings = HDPaths[`${purpose}`]
  if (!pathSettings) throw new Error(`Unknown derivation purpose ${purpose}`)

  const { scriptType } = pathSettings
  const path = ['m', `${purpose}'`, `${coinType || 0}'`]
  const hdPath = { path, scriptType }
  const hdPathInt = { ...hdPath, chain: 'internal' }

  return [
    createPath(account, hdPath, true),
    createPath(account, hdPathInt, true)
  ]
}
